.contacts {
  padding: 120px 100px;
  background-color: $color-cerulean-blue;
  margin: 120px 0 0;

  @include vp-1199 {
    padding: 100px 45px;
    margin-top: 100px;
  }

  @include vp-767 {
    padding: 67px 15px;
    margin-top: 67px;
  }
}

.contacts__container {
  display: flex;
  flex-direction: column;
}

.contacts__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  padding-bottom: 40px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  color: $color-default-white;

  @include vp-1199 {
    flex-direction: column;
    gap: 30px;
    margin-bottom: 30px;
    padding-bottom: 30px;
  }

  & h2 {
    font-size: 50px;
    font-weight: 400;
    line-height: 50px;
    color: $color-default-white;
    margin: 0;

    @include vp-1199 {
      font-size: 34px;
    }

    @include vp-767 {
      font-size: 20px;
      line-height: 20px;
    }
  }
}

.contacts__list {
  display: grid;
  grid-template-columns: 270px auto auto;
  justify-content: end;
  gap: 51px;

  @include vp-1199 {
    gap: 20px 87px;
    justify-content: start;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.contacts__item {
  & dt {
    font-size: 12px;
    font-weight: 400;
    line-height: 12px;
    letter-spacing: 0.12px;
    text-transform: uppercase;
    opacity: 0.8;
    margin: 0 0 24px;

    @include vp-767 {
      margin-bottom: 15px;
    }
  }

  & dd {
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;
    margin: 0;

    @include vp-1199 {
      line-height: 20px;
    }

    @include vp-767 {
      font-size: 14px;
    }
  }
}

.contacts__map {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 500px;
  overflow: hidden;
  border-radius: 16px;

  @include vp-767 {
    height: 240px;
  }
}
