}
@media (max-width: 1199px) {
  .contacts__item dd {
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .contacts__item dd {
    font-size: 14px;
  }
}

.contacts__map {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 500px;
  overflow: hidden;
  border-radius: 16px;
}
@media (max-width: 767px) {
  .contacts__map {
    height: 240px;
  }
}

.map {
  width: 100%;
  height: 100%;
  border-radius: inherit;
  pointer-events: none;
}
.map .leaflet-marker-icon {
  pointer-events: none;
}

.feedback {
  padding: 120px 0 0;
}
@media (max-width: 1199px) {
  .feedback {
    padding-top: 100px;
  }
}
@media (max-width: 767px) {
  .feedback {
    padding-top: 67px;
  }
}

.feedback__container {
  position: relative;
}

.feedback__image {
  position: absolute;
  background-color: rgba(211, 211, 211, 0.5);
  border-radius: 16px;
  inset: 0;
}
.feedback__image img {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 16px;
}

.feedback__form {
  position: relative;
  z-index: 1;
  max-width: 498px;
  height: 100%;
  padding: 83px 74px 31px;
  color: #ffffff;
  background-color: #316dc2;
  border-radius: 16px;
}
@media (max-width: 1199px) {
  .feedback__form {
    max-width: 442px;
    padding: 60px 45px 27px;
  }
}
.feedback__form h2 {
  font-size: 32px;
  font-weight: 400;
  line-height: 36px;
  margin: 0 0 38px;
}
@media (max-width: 1199px) {
  .feedback__form h2 {
    font-size: 28px;
    line-height: 28px;
    margin-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .feedback__form h2 {
    font-size: 18px;
    line-height: 22px;
    margin-bottom: 26px;
  }
}
.feedback__form p {
  font-size: 16px;
  line-height: 26px;
  opacity: 0.9;
  margin: 0 0 30px;
}
@media (max-width: 1199px) {
  .feedback__form p {
    line-height: 20px;
    margin-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .feedback__form p {
    font-size: 14px;
    margin-bottom: 30px;
  }
}

.feedback__input {
  color: rgba(255, 255, 255, 0.3);
  background-color: rgba(255, 255, 255, 0);
}

.footer {
  padding: 60px 0 50px;
}
@media (max-width: 767px) {
  .footer {
    padding: 30px 0 40px;
  }
}

.footer__container {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 25px 52px;
}
@media (max-width: 1199px) {
  .footer__container {
    grid-template-columns: repeat(2, 1fr);
    gap: 25px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    grid-template-columns: 1fr;
  }
}

.footer__nav {
  display: flex;
  align-items: center;
  gap: 52px;
}
@media (max-width: 1199px) {
  .footer__nav {
    grid-column: 1/-1;
  }
}
@media (max-width: 767px) {
  .footer__nav {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
}

.footer__list {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}
@media (max-width: 1199px) {
  .footer__list {
    gap: 15px;
  }
}
@media (max-width: 767px) {
  .footer__list {
    gap: 30px 20px;
    justify-content: center;
  }
}

.footer__link {
  font-size: 16px;
  line-height: 26px;
  text-decoration: none;
}
@media (max-width: 1199px) {
  .footer__link {
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .footer__link {
    font-size: 14px;
  }
}

.footer__link:hover,
.footer__link:focus {
  color: #316dc2;
  outline: none;
}

.footer__link:active {
  color: #275699;
}

.footer__social-list {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  background-color: #316dc2;
  border: none;
  border-radius: 50%;
  color: #ffffff;
}

.footer__social-link:hover,
.footer__social-link:focus {
  color: rgba(255, 255, 255, 0.7);
}

.footer__social-link:active {
  background-color: #275699;
  fill: #ffffff;
}

.footer__bottom-text {
  display: flex;
  justify-content: space-between;
  grid-column: 1/-1;
  gap: 10px;
  font-size: 14px;
  line-height: 18px;
}
@media (max-width: 1199px) {
  .footer__bottom-text {
    flex-direction: column;
    grid-row: 2/3;
    grid-column: 1/2;
    font-size: 12px;
    line-height: 12px;
  }
}
@media (max-width: 767px) {
  .footer__bottom-text {
    line-height: 14px;
    align-items: center;
    gap: 10px;
  }
}
.footer__bottom-text p {
  margin: 0;
}
@media (max-width: 1199px) {
  .footer__bottom-text p {
    opacity: 0.6;
  }
}

.footer__bottom-link {
  text-decoration: underline;
}

.footer__bottom-link:hover,
.footer__bottom-link:focus {
  color: #316dc2;
  outline: none;
}

.footer__bottom-link:active {
  color: #275699;
}

